.amdin-page,
.login-page {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url(../assets/img/shadedBorders.png) left top/100% 100%,
    #111 url(../assets/img/darkNoise.jpg);
  text-align: center;
  z-index: 1000000000;
  line-height: 150%;
  font-size: 20px;
}

.amdin-page .header {
  display: flex;
  flex-direction: row;
  padding: 15px;
  /* background-color: rgb(231, 231, 231); */
  justify-content: space-between;
  align-items: center;
  font-family: "Brothers";
}

.amdin-page .body {
  background-color: blue;
  display: inline-block;
}

.avatar img {
  width: 40px;
  height: 40px;
}
.monitor-wrap {
  padding: 10px;
}

.monitor-title {
  font-size: 25px;
  color: goldenrod;
}

.login-card {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 20%;
  min-width: 250px;
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-form > svg {
  width: 60%;
  min-width: 120px;
}
.login-form > input {
  width: 100%;
  font-size: 16px;
  padding: 8px;
  margin: 8px;
  font-family: "Brothers";
  font-weight: 100;
}
.login-form > button {
  font-size: 16px;
  padding: 8px;
  margin: 5px;
  font-family: "Brothers";
  width: 100px;
  cursor: pointer;
}
.m-card {
  padding: 30px;
  font-size: 30px;
  width: 25%;
}
.m-card-body {
  width: 100%;
  background-color: rgba(240, 248, 255, 0.459);
}

.m-card-group {
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 500px) {
  .m-card {
    width: 100%;
    padding: 0px;
    font-size: 30px;
    padding-top: 7px;
  }

  .m-card-body {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
  }
  .m-card-group {
    display: flex;
    flex-direction: column;
    padding: 3px;
  }
}

.rescet-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 160px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #5e5a5ab6;
  padding: 2px;
}

.rescet-btn:hover {
  font-size: 20px;
  background-color: #a8a1a1b6;
}

.resetloding {
  animation-name: loadSpin;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-duration: 0.6s;
}

@keyframes loadSpin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
