.m-table {
  display: flex;
  flex-direction: column;
}

.m-table-header {
  background-color: rgba(133, 131, 131, 0.712);
}
.m-table-body {
  /* display: flex; */
  /* flex-direction: row; */
  /* padding: 10px; */
  background-color: rgba(192, 190, 190, 0.089);
}

.m-table-row {
  display: flex;
  flex-direction: row;
  padding: 0px;
  justify-content: space-around;
  background-color: rgba(2, 2, 2, 0.13);
  align-items: center;
}
.m-table-row:hover {
  background-color: rgba(36, 34, 34, 0.438);
}
.m-table-cell {
  padding: 6px;
  color: white;
  text-align: center;
  font-size: 15px;
}

.m-table-cell:hover {
}

.block {
  background-color: rgba(3, 136, 3, 0.801);
  border: 0px;
  color: white;
}

.allow {
  background-color: rgba(136, 3, 3, 0.623);
  border: 0px;
  color: white;
}

button {
  cursor: pointer;
}
